* {
    margin: 0;
    padding: 0;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

.wrapper {
    padding: 50px;
    background: #000;

    h1 {
        text-align: center;
        font-size: 40px;
        color: #ffffff;
        padding: 0 0 20px 0;
    }

    p {
        font-size: 20px;
        color: #ffffff;
        padding: 10px;
    }
}

#scrollPath {
    position: fixed;
    top: 0;
    right: 0;
    width: 10px;
    height: 100%;
    background: rgba(255, 255, 255, 0.05);
}

#progressbar {
    position: fixed;
    top: 0;
    right: 0;
    width: 10px;
    height: 100%;
    background: linear-gradient(to top, #008aff, #00ffe7);
    animation: animate 5s linear infinite;
}

